import { Controller } from '@ali/4ever-cangjie';
import Table from '../../mo/models';
import { getClosestContainerBlockFromViewModel, getInnerFullWidth } from '@ali/4ever-utils';

export default function getTableMaxWidthByContainer(controller: Controller, table: Table) {
  const containerBlock = getClosestContainerBlockFromViewModel(controller.view, table);
  const width = getInnerFullWidth(containerBlock, controller.value.document);
  return width ? width - 2 : 0;
}
